/* Subtopic Dropdown */

/*      */
.explore-main-subtopic-dropdown {
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 100%;
  min-width: 0; /*     */
  max-width: 100%;
  background: #fff;
  border: 1px solid #9B9B9B;
  border-radius: 12px;
  transition: box-shadow 0.2s, height 0.3s;
  z-index: 100;
  height: 44px;
  overflow: visible;
  margin-top: 0;
}
.explore-main-subtopic-dropdown.open {
  z-index: 100;
  box-shadow: 0 4px 24px rgba(10,26,51,0.18);
  height: auto !important;
}
.explore-main-subtopic-dropdown-header {
  width: 100%;
  padding: 12px 16px;
  background: none;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 500;
  color: #282828;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  transition: background 0.15s;
}
.explore-main-subtopic-dropdown-header.open {
  border: 1px solid #9B9B9B;
  border-radius: 12px 12px 0 0;
}
.explore-main-subtopic-dropdown-text {
  flex: 1;
  text-align: left;
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 14px;
  color: #4D4D4D;
}
.explore-main-subtopic-dropdown-arrow {
  margin-left: 8px;
  width: 16px;
  height: 16px;
  transition: transform 0.2s;
}
.explore-main-subtopic-dropdown-arrow.open {
  transform: rotate(180deg);
}
.explore-main-subtopic-dropdown-option {
  width: 100%;
  padding: 12px 16px;
  background: none;
  border: none;
  border-bottom: 1px solid #F5F5F5;
  font-size: 16px;
  color: #474747;
  text-align: left;
  cursor: pointer;
  transition: background 0.15s;
  border-radius: 0;
}
.explore-main-subtopic-dropdown-option:hover {
  background: #F5F7FB;
}
.explore-main-subtopic-dropdown-option.last {
  border-bottom: none;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}
.explore-main-subtopic-dropdown-option-text {
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 14px;
  color: #4D4D4D;
  flex: 1;
