@media (max-width: 768px) {
  .no-scrollbar {
    /* IE, Edge */
    -ms-overflow-style: none;
    /* Firefox */
    scrollbar-width: none;
  }

  .no-scrollbar::-webkit-scrollbar {
    /* Webkit   */
    display: none;
  }
}

body {
    background-color: #F4F6FA;
    
    /*    body      . */
    margin: 0;
    
    /* Pretendard      . */
    font-family: Pretendard, Roboto, system-ui, -apple-system, sans-serif;
}

/* =========================================================================
   1.     (Global Layout & Container)
   -       .
========================================================================= */
.nl-container {
    /*    . */
    background-color: #F4F6FA;
    min-height: 100vh;
}


/* =========================================================================
   2.   (Top bar) - 
   - ''    .
   -    .
========================================================================= */
.nl-topbar {
    padding: 16px 16px 12px;

    /*    */
    display: flex;
    align-items: center;
    background-color: #F4F6FA;
    z-index: 3; /*    z-index  */
}

.nl-title {
    /* ''      ( ) */
    color: #474747;
    font-family: Roboto, system-ui, -apple-system, sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: normal;
    letter-spacing: -0.36px;
}


/* =========================================================================
   3.   (Tabs) - 
   - HOT, , My news    .
   -     .
========================================================================= */
.nl-tabs {
    display: flex;
    /*    sticky  */
    position: static;
    padding: 8px 16px 0;
    background-color: #F4F6FA;
    border-bottom: 2px solid #EAEAEA;
}

.nl-tab {
    flex: 1;
    color: #9B9B9B;
    text-align: center;
    font-family: Roboto, system-ui, -apple-system, sans-serif;
    font-size: 20px;
    font-weight: 400;
    line-height: 1.4; /* line-height  */
    letter-spacing: -0.8px;
    padding-bottom: 12px;

    text-decoration: none;
    position: relative;
    border: none;
    background: none;
    cursor: pointer;
    transition: color 0.2s ease-in-out;
}

.nl-tab.active {
    color: #1B1B1B;
    font-weight: 700;
    border-bottom: 2px solid #1B1B1B;
    margin-bottom: -2px;
}

