/* =========================================================================
   4.    (Banner)
   - 'My news   !'  .
========================================================================= */
.nl-banner {
    display: flex;           /*      */
    align-items: center;     /*    */
    gap: 20px;               /*     20px  */

    /*    */
    padding: 10px 16px;      /*   10px,  16px  */
    background: #B8D4FF;
    border-radius: 8px;
    margin: 16px 0 12px;      /*     */
    box-sizing: border-box;

    margin: 16px 16px;
}

.nl-banner-news-img {
    /* clamp(, , )      */
    width: clamp(45px, 15%, 60px);

    height: auto;          /*      */
    aspect-ratio: 1 / 1;   /* 1:1    */
    object-fit: contain;
    flex-shrink: 0;        /*       */
}

.nl-banner-text {
    flex: 1;               /*      */
    min-width: 0;          /*        */
    display: flex;
    flex-direction: column;/*     */
    gap: 4px;              /*     4px  */
}

.nl-banner-title {
    color: #10274A;
    font-family: Roboto;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: -0.36px;
    margin: 0;
}

.nl-banner-desc {
    color: #122C54;
    font-family: Roboto;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.24px;
    margin: 0;
}


/* =========================================================================
   5.     (List, Card)
   -      .
========================================================================= */
.nl-section {
    /*       */
    padding-bottom: 24px;
}

.nl-list-meta {
    /* "N  "   */
    color: var(--Text-color_box, #616161);
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -0.56px;
    margin: 16px;
}

.nl-card-link {
    text-decoration: none; /*      . */
    color: inherit;        /*      . */
    display: block;        /* a  div  . */
}

.nl-card {
    display: flex;
    flex-direction: column;
    gap: 10px;
    background: #fff;
    border-radius: 14px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
    margin: 0 32px 16px;
    overflow: hidden; 

    transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.nl-card-link:hover .nl-card {
    transform: translateY(-4px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
}

.nl-card-image {
    width: 100%;
    height: 160px;
    
    border-radius: 14px 14px 0 0;
    
    background-size: cover;
    background-position: center;
}

.nl-card-content {
    display: flex;
    flex-direction: column;
    padding: 0 12px 16px;
    gap: 8px;
}

.nl-card-tags {
    display: flex;       /*     */
    flex-wrap: wrap;     /*      */
    gap: 6px;            /*    */
}

.nl-badge {
     padding: 4px 8px;
    
    font-family: Roboto, system-ui, -apple-system, sans-serif;
    display: inline-block;
    font-size: 12px;
    border-radius: 999px;
    background: #DCEAFF;
    color: #234E8F;
    font-weight: 500;
    
    align-self: flex-start;
}

.nl-card-title {
    color: #000;

    font-family: Roboto;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
    line-height: 1.4;
    margin: 0;
    letter-spacing: -0.01em;

    white-space: pre-line;
}

.nl-card-sub {
    color: #474747;

    font-family: Roboto;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.24px;
    margin: 0;
}

.nl-card-meta {
    display: flex;
    gap: 12px;
    font-size: 12px;
    color: #9AA0A6;
    margin-top: 4px; /*    */
}

.nl-card-meta span {
    display: flex;
    align-items: center;
    gap: 4px; /*    */
}

/* =========================================================================
   6.    (Empty State)
   - 'My news'       .
========================================================================= */
.nl-empty {
    padding: 24px;
    text-align: center;
    color: #5F6368;
    background: #F7F8FA;
    border-radius: 12px;
}

/* =========================================================================
   7.    (Category Filters) -  
   - ''      .
========================================================================= */
.nl-category-filters {
    display: flex;
    gap: 8px;
    /* [ ] / 8px, / 16px  */
    margin: 8px 16px;
    
    overflow-x: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.nl-category-filters::-webkit-scrollbar {
    display: none;
}

.nl-category-filters + .nl-banner {
    margin-top: 0; /*      margin-top 0  */
}

/*      */
.nl-category-btn {
    /*      */
    display: inline-flex;
    align-items: center;
    justify-content: center;

    /*   */
    padding: 8px 16px;
    border-radius: 999px;
    border: 1px solid #DFE5EE;
    background-color: #EEF2F6;

    /* :      line-height 1  */
    color: #626262;
    font-size: 14px;
    font-weight: 500;
    line-height: 1; /* :      */

    /*  */
    cursor: pointer;
    white-space: nowrap;
    transition: all 0.2s ease-in-out;
}

.nl-category-btn.active {
    background: linear-gradient(104deg, #448FFF -6.51%, #4833D0 105.13%);
    color: #fff;
    border-color: transparent;
    font-weight: 700;
}