.quiz-question-container {
  width: 100%;
  min-height: 100vh;
  max-width: 100%;
  margin: 0 auto;
  background: #F4F6FA;
  box-sizing: border-box;
  overflow-y: auto;
  position: relative;
  /* no side padding here! */
}

.quiz-question-header {
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 16px;
  padding-bottom: 16px;
}

.quiz-question-title {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 2.125rem;
  letter-spacing: -0.04em;
  color: #1B1B1B;
  margin-bottom: 16px;
}

/* Article text block (title + body) */

/* Article image wrapper and image */
/*   :  16px ,  100% */
/*   :   16px ,  100% */
/* Article image wrapper: full width, no side margin, spacing below */
/*     */
.article-image-wrap {
  width: 106.2%;
  margin-left: -12px;
  margin-right: -12px;
  margin-bottom: 12px;
  box-sizing: border-box;
  background: none;
}

/*    */
.article-image-wrap img,
.quiz-question-article-img {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 12px;
  object-fit: cover;
  background: #f4f6fa;
}


/* Article card: full width, spacing below, padding for text */
.quiz-question-article-text-block {
  width: 100%;
  background: transparent;
  border-radius: 0;
  box-shadow: none;
  padding: 0;
  margin: 0 0 20px 0;
  box-sizing: border-box;
  align-self: stretch;
}

/* Add spacing between article card and options */
.quiz-question-options-block {
  margin-top: 12px;
}
/* gradient removed */

.quiz-question-option-card {
  width: 100%;
  margin-bottom: 16px;
  padding: 19px 16px;
  border-radius: 8px;
  background: #fff;
  cursor: pointer;
  border: 1px solid transparent;
  box-shadow: 0px 0px 2px rgba(0,0,0,0.25);
  transition: border 0.2s, box-shadow 0.2s;
}
.quiz-question-option-card.selected {
  border: 1px solid #448FFF;
  box-shadow: 0px 0px 2px #448FFF;
}
.quiz-question-option-card.correct {
  border: 1px solid #1EE000;
  box-shadow: 0px 0px 2px #1EE000;
}
.quiz-question-option-card.wrong {
  border: 1px solid #EE3030;
  box-shadow: 0px 0px 2px #EE3030;
}
.quiz-question-option-row {
  display: flex;
  align-items: center;
  gap: 16px;
}
.quiz-question-option-badge {
  width: 36px;
  height: 36px;
  border-radius: 18px;
  background: #448FFF;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  color: #fff;
  flex-shrink: 0;
}
.quiz-question-option-badge.correct {
  background: #1EE000;
}
.quiz-question-option-badge.wrong {
  background: #FF5959;
}
.quiz-question-option-text {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.01em;
  color: #4D4D4D;
  flex: 1;
  white-space: normal;
  overflow-wrap: break-word;
  word-break: keep-all;
}

.quiz-question-explanation {
  margin-top: 16px;
  background: #E6F0FF;
  border-radius: 8px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding-left: 24px;
}
.quiz-question-explanation-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
}
.quiz-question-explanation-label {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 18px;
  color: #474747;
  padding: 0;
  background: transparent;
  border-radius: 0;
  box-shadow: none;
}
.quiz-question-explanation-badge {
  width: 24px;
  height: 24px;
  border-radius: 18px;
  background: #448FFF;
  display: flex;
  justify-content: center;
  align-items: center;
}
.quiz-question-explanation-badge-text {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  color: #fff;
}
.quiz-question-explanation-text {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.02em;
  color: #647184;
  white-space: pre-line;
}

.quiz-question-learn-btn {
  width: 100%;
  padding: 16px;
  border-radius: 8px;
  background: #FFFFFF;
  box-shadow: 0px 0px 12px rgba(0,0,0,0.08);
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.quiz-question-learn-btn-label {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: -0.02em;
  color: #000;
}


/*    */
.quiz-question-learning-wrap {
  margin-top: -6px;
  margin-bottom: 44px;
  width: 100%;
}

/*      */
.quiz-question-learning-svg-wrap {
  position: relative;
  width: 100%;
  display: grid;                     /* / 2  */
  grid-template-columns: 1fr 72px;   /* : , :  */
  column-gap: 16px;                  /* -  16px */
  align-items: start;                   /*        */
}

/* :      */
.quiz-question-learning-bubble {
  position: relative;         /* ::after   */
  display: inline-block;
  background: #448FFF;
  color: #fff;
  /*  :    */
  margin-left: 16px;
  border-radius: 14px;
  padding: 10px 12px;
  min-height: 38px;
  width: 97%;
  max-width: 100%;             /*     100% */
  word-break: keep-all;
  overflow-wrap: break-word;
  box-sizing: border-box;
  justify-self: start;         /*   */
  align-self: end;             /*      */
}

/*  :     10px */
.quiz-question-learning-bubble::after{ 
  content: '';
  position: absolute;
  right: -10px;
  top: 86%;
  transform: translateY(-50%) rotate(25deg);
  width: 20px;
  height: 20px;
  background: #448FFF;
  clip-path: polygon(0 0, 100% 50%, 0 100%);
  border-radius: 3px;
  box-sizing: border-box;
}

/* :      ( ,  ) */
.quiz-question-learning-ant {
  position: static;     /*    */
  width: 72px;
  height: 72px;
  z-index: 3;           /*   */
  pointer-events: none;
  justify-self: end;     /*   */
  align-self: end;       /*   */
  transform: translateY(22px); 
}

/* Ant character */
/*   :    (  inline style    ) */


.quiz-question-learning-svg-label {
  position: relative;
  display: block;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  color: #FFFFFF;
  word-break: keep-all;
  overflow-wrap: break-word;
}
.quiz-question-learning-chalkboard-wrap {
  position: relative;
  height: auto; /*      */
  margin-top: 12px;
  width: 100%;
  box-sizing: border-box;
  padding-left: 14px;
  padding-right: 14px;
}
.quiz-question-learning-chalkboard-inner {
  width: 100%;
  min-width: 0;
  max-width: unset;
  box-sizing: border-box;
  position: relative;
  z-index: 2; /*    */
  background: #4B794C;         /*   */
  border-radius: 4px;
  overflow: hidden;            /*     */
  padding-bottom: calc(16px + 6px + 19px); /*   16px +  6px +  19px */
  z-index: 0;
}
.quiz-question-learning-chalkboard-text {
  position: relative;
  z-index: 1;
  padding: 24px 16px 0; /*  24px,  16px,  0 ( padding-bottom  16px ) */
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.02em;
  color: #FFFFFF;
  /*  /   */
  word-break: break-word;    /*     */
  overflow-wrap: anywhere;   /*     */
  white-space: pre-line;     /*      */
  box-sizing: border-box;
  width: 100%;
  min-width: 0;              /*   flex   */
  height: auto;              /*    */
  margin-top: 0;             /*       */
  /*  /   */

}

/*      CSS  () */
.quiz-question-chalk-line {
  position: absolute;
  height: 6px;
  bottom: 19px;              /*     */
  border-radius: 2px;
}
.quiz-question-chalk-line.white {
  background: #FFFFFF;
  left: 8.5%;
  width: 19%;
}
.quiz-question-chalk-line.red {
  background: #FF5959;
  left: 31.8%;
  width: 19%;
}
.quiz-question-chalk-bottom-bar {
  position: absolute;
  left: -2px;    /*  6px  */
  right: -2px;   /*  6px  */
  bottom: 0;
  height: 19px;
  background: #7D5F5F;
  border-radius: 4px;
  z-index: 1;
}

.quiz-question-point-wrap {
  margin-top: 24px;
}
.quiz-question-point-card {
  background: #FFE478;
  box-shadow: 0px 0px 12px rgba(0,0,0,0.08);
  border-radius: 12px;
  padding: 16px;
  position: relative;
}
.quiz-question-point-title {
  margin-bottom: 16px;
  font-family: 'Roboto', sans-serif;
  font-weight: 800;
  font-size: 24px;
  line-height: 21px;
  letter-spacing: -0.02em;
  color: #000;
}
.quiz-question-point-content {
  width: 100%;               /*     */
  max-width: 100%;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 20px;
  padding-left: 0;
  padding-right: 0;
  box-sizing: border-box;
  white-space: normal;       /*   */
  word-break: break-word;    /*     */
  overflow-wrap: anywhere; 
}
.quiz-question-point-content-pre {
  white-space: pre-line;
}
.quiz-question-point-hint-blur {
  position: absolute;
  inset: 0;
  border-radius: 12px;
  backdrop-filter: blur(6px);
  display: flex;
  justify-content: center;
  align-items: center;
}
.quiz-question-point-hint-btn {
  border-radius: 12px;
  background: transparent;
  font-weight: 700;
  font-size: 14px;
  border: none;
  cursor: pointer;
}

.quiz-question-bottom-btn-wrap {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 100%;
  margin: 0 auto;
  
  padding: 16px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  z-index: 10;
}
.quiz-question-bottom-btn {
  width: 100%;
  padding: 16px;
  border-radius: 8px;
  border: none;
  background: linear-gradient(104.45deg, #448FFF -6.51%, #4833D0 105.13%);
  color: #fff;
  font-weight: 700;
  font-size: 18px;
  cursor: pointer;
  transition: background 0.2s;
}
.quiz-question-bottom-btn:disabled {
  background: #CACACA;
  cursor: not-allowed;
}


